import fetch from 'isomorphic-fetch';

/**
 * Fetch wrapper. Reject on 4xx and 5xx responses, otherwise should have no effect
 *
 * @private
 */
export default function rejectingFetch(...args) {
  return fetch(...args)
  .then((resp) => {
    if (process.env.NODE_ENV === 'test') {
      if (resp.status >= 400) {
        //in test, log the error, its annoying to explicitly catch to log it since often returned
        resp.clone().text().then((text) => {
          console.log(text); //eslint-disable-line no-console
        });
      }
    }

    if (resp.status >= 400) {
      return Promise.reject(resp);
    }
    return resp;
  });
}

export { fetch };



