import { generatePseudoMd5, getSequencesFromMap } from '../utils/sequenceMd5';
import { headersGet, headersPost } from './utils/headers';
import { dataApiPath } from './utils/paths';
import rejectingFetch from './utils/rejectingFetch';

let cache;
if (process.env.BROWSER) {
  const caching = require('../utils/cache/index'); //eslint-disable-line global-require
  cache = caching.createInstance({ storeName: 'constructor_sequences' });
}

const getSequenceUrl = (md5, range) => {
  if (range) {
    const psuedoMd5 = generatePseudoMd5(md5, range);
    return dataApiPath(`sequence/${psuedoMd5}]`);
  }
  return dataApiPath(`sequence/${md5 || ''}`);
};

//cache by psuedo-md5 (or md5 if whole sequence)... no deduping

const cacheGetSequence = (md5) => {
  if (cache) {
    return cache.getItem(md5);
  }
  return Promise.resolve(null);
};

const cacheSequence = (md5, sequence) => {
  if (cache) {
    return cache.setItem(md5, sequence);
  }
  return Promise.resolve();
};

/**
 * @private
 * @param {string} md5 pseudoMd5 OR realMd5
 * @param {Array} [range]
 */
export const getSequence = (md5, range) => {
  if (!md5) {
    return Promise.resolve(null);
  }

  //only fetch the range we need by creating pseudoMd5, and cache properly (include md5, no dedupe)
  const psuedoMd5 = generatePseudoMd5(md5, range);
  const url = getSequenceUrl(psuedoMd5);

  return cacheGetSequence(psuedoMd5)
  .then((seq) => {
    if (seq) {
      return seq;
    }

    //if not in cache, fetch the item from server
    return rejectingFetch(url, headersGet())
    .then(resp => resp.text())
    .then((sequence) => {
      cacheSequence(psuedoMd5, sequence);
      return sequence;
    });
  });
};

/**
 * Get multiple sequences
 * dedupes requests across multiple md5s and fetches union byte range, then reassociate with blocKId
 *
 * @private
 * @param {Object} blockIdsToMd5s expects map in form { blockId: pseudoMd5 }
 * @returns {Object} in form { blockId: sequence }
 */
export const getSequences = blockIdsToMd5s =>
  getSequencesFromMap(blockIdsToMd5s, (realMd5, range) => getSequence(realMd5, range));

/**
 * Write a sequence to the server, which computes the hash of the sequence it is saved under
 *
 * @private
 * @param sequence
 */
export const writeSequence = (sequence) => {
  const url = getSequenceUrl();
  const stringified = JSON.stringify({ sequence });

  return rejectingFetch(url, headersPost(stringified))
  .then(resp => resp.text())
  .then((md5) => {
    //no need to wait for resolution
    cacheSequence(md5, sequence);
    return md5;
  });
};



