import invariant from 'invariant';
import _ from 'lodash';

/**
 * Helper class for ProjectTree
 *
 * node has fields { data, parent, children } and expected to include { depth, inList }
 * data can be a Block, Project (or anything really)
 *
 * @private
 */

export class Node {
  constructor(data, info) {
    //invariant(Number.isInteger(info.depth), 'should specify depth');
    const { parent = null, children = [], ...rest } = info;
    Object.assign(this, rest, { parent, children, data });
  }

  addChild(child) {
    child.parent = this;
    this.children.push(child);
  }

  //ascending order, first is immediate parent, last is root (project)
  parents() {
    const parents = [];

    function addToPath(node) {
      if (!node) {
        return;
      }
      parents.push(node);
      addToPath(node.parent);
    }

    addToPath(this.parent);
    return parents;
  }

  //can pass either a node or an id
  hasParent(node) {
    let parent = this.parent;
    while (parent) {
      if (parent === node || parent.data.id === node) {
        return true;
      }
      parent = parent.parent;
    }
    return false;
  }

  contents() {
    return [].concat(this.children, ...this.children.map(node => node.contents()));
  }

  //get index in parent 'components' (special knowledge of Project / Block data model)
  index() {
    if (this.isRoot()) {
      return -1;
    }
    return this.parent.data.components.indexOf(this.data.id);
  }

  isRoot() {
    return !this.parent;
  }

  isLeaf() {
    return this.children.length === 0;
  }

  //easily get some common info about the node
  info() {
    return {
      block: this.data,
      blockId: this.data.id,
      isLeaf: this.isLeaf(),
      isList: this.data.rules.list === true,
      inList: this.inList === true,
      depth: this.depth,
    };
  }
}

/**
 * Class with utilities for walking the tree of blocks in a project. Generated e.g. from Rollup.tree()
 *
 * Tree is composed of Nodes (defined above)
 *
 * Includes list blocks, where list options have a special field { inList = true }
 *
 * NB - does not handle circular trees (could check _map in parse())
 *
 * The ProjectTree is static, and must be re-caclucated on each mutation to the rollup
 * future - this is computed as part of the rollup object dynamically. But do not use this for mutations, so they do not get out of sync
 * @name ProjectTree
 * @class
 * @gc Model
 */
export default class ProjectTree {
  /**
   * Given a rollup, generate a ProjectTree
   * @param {Rollup} rollup
   */
  constructor(rollup) {
    invariant(rollup && rollup.blocks && rollup.project, 'must pass a rollup instance');

    // don't need to validate if already a valid Rollup
    // Rollup.validate(rollup, true);

    this._rollup = rollup;

    // map { ID: Node } where ID is projectId or blockId
    this._map = new Map();

    const parse = (rollup, parent, depth = 0, inList = false) => {
      const root = parent ?
        new Node(parent, { depth, inList }) :
        new Node(rollup.project, { depth, inList });

      //store all nodes by ID in a map
      this._map.set(root.data.id, root);

      // traverse contents (components + options)
      const componentIds = root.data.components;
      const optionIds = Object.keys(root.data.options || {}); //might be a project

      for (let index = 0; index < componentIds.length; index++) {
        const block = rollup.blocks[componentIds[index]];
        if (!block) {
          //this could happen if parent was deleted first
          throw new Error(`must have a block, got undefined / null option: ${componentIds[index]}`);
        }
        root.addChild(parse(rollup, block, depth + 1));
      }

      for (let index = 0; index < optionIds.length; index++) {
        const option = rollup.blocks[optionIds[index]];
        if (!option) {
          //this could happen if parent was deleted first
          throw new Error(`must have a block, got undefined / null option: ${optionIds[index]}`);
        }
        //list options don't have components, but recurse so consistently create node + add to map
        root.addChild(parse(rollup, option, depth + 1, true));
      }

      return root;
    };

    this._root = parse(rollup);
  }

  /**
   * Get a node by ID (can be projectId or blockId)
   * @function
   * @param {UUID} [id] ID of block
   * @returns {Node}
   */
  getNode(id) {
    //return undefined if we get no ID, so use default params
    if (!id) {
      return undefined;
    }
    return this._map.get(id);
  }

  /**
   * Visit all nodes in the tree, depth first
   * Stop traversal by returning false.
   *
   * Can visit pre-order or post-order
   * https://en.wikipedia.org/wiki/Tree_traversal
   *
   * @function
   * @param {Function} callback called with Node { data, parent, children } -- return false to skip subsequent calls
   * @param {Node} [root]
   * @param {Object} options
   *    [postOrder=false] true to call callback post-order (default pre-order)
   *    [includeProject=false] Include project as a node (the root node)
   *    [includeListOptions=true] Include list options in traversal, or just traverse the list block alone (note that the list block will return isLeaf === true)
   */
  traverseDepthFirst(callback, root = this._root, options = {}) {
    invariant(typeof callback === 'function', 'must pass function callback');
    const { postOrder = false, includeProject = false, includeListOptions = true } = options;

    //breaking out of traversal
    let shouldContinue = true;

    function recurse(node) {
      //pre-order = call as going down
      if (postOrder !== true && (node.parent || includeProject === true) && shouldContinue) {
        shouldContinue = shouldContinue && callback(node) !== false;
      }

      for (let index = 0; index < node.children.length; index++) {
        if (shouldContinue) {
          //check if children are of a list block, skip if desired
          if (node.data.rules.list !== true || includeListOptions) {
            recurse(node.children[index]);
          }
        }
      }

      //post-order = call as coming back up
      if (postOrder === true && (node.parent || includeProject === true) && shouldContinue) {
        shouldContinue = shouldContinue && callback(node) !== false;
      }
    }

    recurse(root);
  }

  /**
   * Visit nodes in tree, breadth-first.
   * Stop traversal by returning false.
   *
   * @function
   * @param {Function} callback called with Node { data, parent, children }
   * @param {Node} [root]
   * @param {Object} options
   *    [includeProject=false] Include project as a node (the root node)
   *    [includeListOptions=true] Include list options in traversal
   */
  traverseBreadthFirst(callback, root = this._root, options = {}) {
    invariant(typeof callback === 'function', 'must pass function callback');
    const { includeProject = false, includeListOptions = true } = options;

    const queue = [root];

    function processQueue() {
      if (queue.length === 0) {
        return;
      }

      const node = queue.shift();

      for (let index = 0; index < node.children.length; index++) {
        if (node.data.rules.list !== true || includeListOptions) {
          queue.push(node.children[index]);
        }
      }

      //if have a project, call the callback (continue if have project)
      //if callback returns false, stop traversing
      if (node.parent || includeProject === true) {
        if (callback(node) !== false) {
          processQueue();
        }
      } else {
        processQueue();
      }
    }

    processQueue();
  }

  /**
   * Filter all nodes in the tree, using depthFirst traversal
   * @function
   * @param callback
   * @param constructId
   * @param options
   * @returns {Array}
   */
  filter(callback, constructId, options) {
    invariant(typeof callback === 'function', 'must pass function callback');

    const nodes = [];
    const root = this.getNode(constructId);

    this.traverseDepthFirst((node) => {
      if (callback(node) === true) {
        nodes.push(node);
      }
    }, root, options);

    return nodes;
  }

  leaves(constructId, options) {
    return this.filter(node => node.isLeaf(), constructId, options);
  }

  /**
   * Given a list of blocks, sort the blocks in order, using depth-first traversal
   *
   * todo - ability to omit parents / nested selections
   *
   * @function
   * @param blockIds
   * @param [constructId] optionally pass root construct ID
   */
  sortBlocks(blockIds, constructId) {
    const blockIdMap = _.zipObject(blockIds, blockIds);
    return this.filter(node => typeof blockIdMap[node.data.id] === 'string', constructId);
  }

  /**
   * Check whether a selection of blockIds is contiguous
   *
   * @function
   * @param {Array<UUID>} blockIds Block IDs to check
   * @param {UUID} constructId ID of construct
   * @param options
   *    [options.includeNested] If true, all blocks nested in a selected parent block are considered contiguous and included under the parent
   * @returns {Array<Node>|boolean} False if not contiguous, array of blocks in selection if contiguous
   */
  isSelectionContiguous(blockIds, constructId, options = {}) {
    // ensure blockIds are unique
    const uniqueBlockIds = Array.from(new Set(blockIds));
    const { includeNested = true } = options;
    const root = this.getNode(constructId);
    const blockMap = uniqueBlockIds.reduce((acc, blockId) => Object.assign(acc, { [blockId]: true }), {});
    let tracking = null; //track if a parent was selected
    const matches = []; //track the blocks in the selection (including nested if specified)
    let numMatches = 0;

    this.traverseDepthFirst((node) => {
      const { blockId, isLeaf, isList, depth } = node.info();
      const inSelection = blockMap[blockId] === true;

      //keep tracking if we had a parent strictly higher up (less depth), otherwise clear
      tracking = (includeNested && tracking && tracking.depth < depth) ? tracking : null;

      //have non-contiguous selection, and not tracking a parent, exit traversal
      if (!tracking && !inSelection && (isLeaf || isList) && numMatches > 0) {
        return false;
      }

      if (inSelection) {
        tracking = tracking || node; //start tracking if we aren't already
        numMatches++;
        matches.push(node);
      } else if (tracking && includeNested) {
        //if tracking (parent selected) or block in selection, save it
        matches.push(node);
      }

      //contiguous and were done + can exit traversal
      if (numMatches === uniqueBlockIds.length && (!tracking || !includeNested)) {
        return false;
      }
    }, root);

    return numMatches === uniqueBlockIds.length ? matches : null;
  }

  /**
   * Get the global sequence START positions of all blocks and annotations
   *
   * By default, list options count as zero-length. This is to make mapping annotations easier.
   * You can pass listOptions = { ...optionMap } to specify which list options to use, and calculate lengths where list blocks use the specified option.
   * @private
   * @param [constructId] Specify construct as root, so only traverse that construct
   * @param [listOptions=null] Object of preferred list options, otherwise choose first
   * @param {Object} options
   *    [options.includeAnnotations=true] Include all annotations positions in the map returned
   *    [options.ends=false] Find the end positions, rather than start positions (used internally to find ranges)
   * @returns {Object} Map of { blockId: position }
   */
  _getPositions(constructId, listOptions = null, options = {}) {
    const { includeAnnotations = true, ends = false } = options;
    let offset = 0;
    const positions = {};
    const root = this.getNode(constructId);

    if (root) {
      invariant(root.depth <= 1, 'must pass root or construct if pass root (or offsets will be off)');
    }

    //NB - since ends -> postOrder and !ends -> preOrder, can't do these at the same time

    this.traverseDepthFirst((node) => {
      const { block, isLeaf, isList, inList } = node.info();

      //save the start position
      if (ends !== true) {
        positions[block.id] = offset;

        //if we want the positions of annotations, add them to the accumulator
        if (includeAnnotations === true) {
          block.sequence.annotations.forEach((annotation) => {
            positions[annotation.id] = offset + annotation.start;
          });
        }
      }

      // update the offset if:
      // have a leaf node (and its not in a list)
      // have a list block (and use its desired option)
      if ((isLeaf && (listOptions || !inList)) || (isList && listOptions)) {
        if (isList) {
          const opts = Object.keys(block.options);
          const option = listOptions[block.id] || opts.find(opt => block.options[opt]) || opts[0];
          offset += option.sequence.length || 0;
        } else {
          offset += block.sequence.length || 0;
        }
      }

      //save the end position
      if (ends === true) {
        positions[block.id] = offset;

        //if we want the positions of annotations, add them to the accumulator
        //NB - offset is the end of the block, so adjust annotations accordingly
        if (includeAnnotations === true) {
          block.sequence.annotations.forEach((annotation) => {
            positions[annotation.id] = offset - (offset - annotation.end);
          });
        }
      }
    }, root, { postOrder: ends === true, includeListOptions: false });

    return positions;
  }

  /**
   * Get the ranges (start, end, length) of blocks and annotations
   * @param constructId
   * @param listOptions
   * @param {Object} options
   *    [options.skipEmpty=false] Skip empty blocks
   *    [options.includeAnnotations=true] Include all annotations positions in the map returned
   * @returns {Object} object with starts, ends, and ranges in form { start, ends, [block.id]: { start, end, length } }
   */
  getRanges(constructId, listOptions = null, options = {}) {
    const { skipEmpty = false, ...restOptions } = options;

    //NB - perf - this traverses tree twice (pre order for start, post order for ends)
    const starts = this._getPositions(constructId, listOptions, { ...restOptions, ends: false });
    const ends = this._getPositions(constructId, listOptions, { ...restOptions, ends: true });

    //calculate ranges, skipping empty if specified
    const ranges = _.reduce(starts, (acc, start, blockId) => {
      const end = ends[blockId];
      const length = end - start;
      if (skipEmpty && !length) {
        return acc;
      }
      return Object.assign(acc, { [blockId]: { start, end, length, blockId } });
    }, {});

    return {
      starts,
      ends,
      //keyed by block / annotation ID and object in form { start, end, length, blockId }
      ...ranges,
    };
  }

  /**
   * Check whether a selection of blockIds is contiguous and within a single level of the construct hierarchy
   *
   * @function
   * @param {Array<UUID>} blockIds Block IDs to check
   * @param {UUID} constructId ID of construct
   * @returns {Array<String>} if true return the ids of the blocks sorted by order they appear in the construct, otherwise null
   */
  isSelectionContiguousSingleLevel(blockIds, constructId) {
    if (blockIds.length < 2) {
      return null;
    }
    // ensure blockIds are unique
    const uniqueBlockIds = Array.from(new Set(blockIds));
    const depths = [];
    const blockIdsOrdered = [];
    this.traverseDepthFirst((node) => {
      if (uniqueBlockIds.indexOf(node.data.id) >= 0) {
        depths.push(node.depth);
        blockIdsOrdered.push(node.data.id);
      }
    }, this.getNode(constructId));
    invariant(depths.length === uniqueBlockIds.length, 'expected to find all blocks');
    // all depths should be the same, if so return the block ids sorted
    return depths.every(depth => depth === depths[0]) ? blockIdsOrdered : null;
  }

  /**
   * get all empty blocks from the given root, which could be the construct id or a blockId.
   * Blocks with no sequence but children with sequence are not considered empty.
   * The root is not included in the result even if empty.
   * @param rootId
   */
  getEmptyBlocks(rootId) {
    const emptyBlockIds = [];
    const traverse = (node) => {
      // add this nodes sequence first
      let blockSequenceLength = node.data.sequence.length;
      // add in children
      node.children.forEach((node) => {
        blockSequenceLength += traverse(node);
      });
      // if this node is empty including children and it is not the construct add to empty blocks
      if (blockSequenceLength === 0 && node.data.id !== rootId) {
        emptyBlockIds.push(node.data.id);
      }
      return blockSequenceLength;
    };
    traverse(this.getNode(rootId));
    return emptyBlockIds;
  }

  /**
   * select subtree identified by block at the root of the subtree.
   * NOTE: results do NOT include rootId
   * @param rootId
   */
  selectNestedBlocks(rootId) {
    const subTreeIds = [];
    const traverse = (nodes) => {
      nodes.forEach((node) => {
        subTreeIds.push(node.data.id);
        traverse(node.children);
      });
    };
    traverse(this.getNode(rootId).children);
    return subTreeIds;
  }

  /**
   * Given a list of annotations, find the lowest common ancestor for each
   * @param {UUID} constructId
   * @param {Array<Object>} annotations objects with fields { id, start, end }
   * @param {Object|null} [listOptions]
   * @returns {Object} in form { [blockId]: [ annotation, ... ] }
   */
  findCommonAncestors(constructId, annotations, listOptions) {
    invariant(typeof constructId === 'string', 'must pass constructId');
    invariant(Array.isArray(annotations), 'array of valid annotations');
    invariant(annotations.every(ann => Number.isInteger(ann.start) && Number.isInteger(ann.end)), 'must have start, end');

    const { starts, ends, ...ranges } = this.getRanges(constructId, listOptions); //eslint-disable-line no-unused-vars

    //todo - it may be faster to just recurse through the construct, rather than traverse whole tree, since we know the positions

    return _.reduce(annotations, (acc, annotation) => {
      const { start, end } = annotation;
      const overlaps = _.filter(ranges, range => range.start <= start && range.end >= end);
      const nodes = overlaps.map(range => this.getNode(range.blockId));
      const lowestCommonAncestor = _.maxBy(nodes, node => node.depth);
      const blockId = lowestCommonAncestor.data.id;

      if (acc[blockId]) {
        acc[blockId].push(annotation);
        return acc;
      }

      return Object.assign(acc, { [blockId]: [annotation] });
    }, {});
  }
}



