import { symbolMap } from '../inventory/roles';
import ProteinSchema from './Protein';
import Schema from './SchemaClass';
import fields from './fields/index';

/**
 * An annotation on a sequence.
 * Should either have a sequence or start+end location
 * Later, we may want to make a field that notes how optimizable the sequence is. E.g. none, codon-optimize, or random sequence. It may be represented as a degenerate sequence?
 *
 * @name AnnotationSchema
 * @memberOf module:Schemas
 * @gc Schema
 */

const fieldDefs = {
  id: [
    fields.id({ prefix: 'annotation' }).required,
    'ID of annotation',
  ],
  name: [
    fields.string().required,
    'Name of annotation',
  ],
  description: [
    fields.string(),
    'Description of annotation',
  ],
  color: [
    fields.string(),
    'Color of the Annotation',
  ],
  role: [
    fields.oneOf(Object.keys(symbolMap)),
    'Role of the Annotation',
  ],
  tags: [
    fields.object(),
    'Dictionary of tags defining annotation',
  ],
  start: [
    fields.number({ min: 0 }),
    'Location of start of annotation',
    { avoidScaffold: true },
  ],
  end: [
    fields.number({ min: 0 }),
    'Location of end of annotation',
    { avoidScaffold: true },
  ],
  direction: [
    fields.oneOf(['forward', 'reverse', 'none']),
    'Direction of the Annotation',
    { avoidScaffold: true },
  ],
  notes: [
    fields.object().required,
    'Notes about the annotation',
  ],
  block: [
    fields.object(),
    'Block information, if the annotation came from a block',
    { avoidScaffold: true },
  ],
  computed: [
    fields.string(),
    'If the annotation was computed, rather than user generated, source of computation',
    { avoidScaffold: true },
  ],
  protein: [
    ProteinSchema,
    'Protein information (PDBs, molecule viewer session)',
  ],
};

export class AnnotationSchemaClass extends Schema {
  constructor(fieldDefinitions) {
    super(Object.assign({}, fieldDefs, fieldDefinitions));
  }
}

export default new AnnotationSchemaClass();



