
export const getPrefix = () => {
  try {
    //this only works after the page has loaded... fails when the reducers are loading for example since store not initiated
    return window.constructor.store.getState().user.userid;
  } catch (err) {
    //assume we're in the browser, and try to get the user ID based on what is sent in index template
    try {
      //since we refresh when we log the user out, this should be fine
      return window.flashedUser.userid;
    } catch (err) {
      //handle when we're testing and window not available
      if (process.env.NODE_ENV === 'test') {
        return 'test';
      }

      //otherwise we're on the server or something
      return 'constructor';
    }
  }
};

export const prefixKey = (key) => {
  const prefix = getPrefix();
  return `${prefix}_${key}`;
};

export default prefixKey;



