import _ from 'lodash';
import invariant from 'invariant';

import { symbolMap } from '../inventory/roles';
import AnnotationSchema from '../schemas/Annotation';
import { nextColorHex, paletteIndexToHex, hexToPaletteIndex } from '../utils/color/index';
import Immutable from './Immutable';
import Block from './Block'; //circular dependency
import * as Directions from '../constants/directions';

/**
 * Annotations mark regions of sequence with notes, colors, roles, etc.
 * Annotations are often used in imports due to the hierarchical nature of the Genetic Constructor data model. Blocks do not allow for overlaps, but many sequences have overlapping annotations. Annotations which do not overlap are used to create the Block hierarchy, while overlaps are converted into instances of the Annotation class.
 * @name Annotation
 * @class
 * @extends Immutable
 * @gc Model
 */
export default class Annotation extends Immutable {
  /**
   * Create an annotation
   * @constructor
   * @param {Object} input Input object for the annotation to merge onto the scaffold
   * @param {boolean} [immutable=true]
   */
  constructor(input, immutable = true) {
    const scaff = AnnotationSchema.scaffold();
    scaff.color = nextColorHex();
    super(_.merge(scaff, input), immutable);

    //validate the model. This is slow, especially for lots of blocks (e.g. page load)
    if (process.env.NODE_ENV !== 'production') {
      Annotation.validate(this, true);
    }

    return this;
  }

  /**
   * Create an unfrozen annotation, extending input with schema
   * @method classless
   * @memberOf Annotation
   * @param {Object} [input]
   * @returns {Object} an unfrozen JSON, no instance methods
   */
  static classless(input) {
    return Object.assign({}, new Annotation(input, false));
  }

  /**
   * Validate an annotation
   * @method validate
   * @memberOf Annotation
   * @static
   * @param {Object} input Object to validate
   * @param {boolean} [throwOnError=false] Validation should throw on errors
   * @throws if you specify throwOnError
   * @returns {boolean} Whether input valid
   */
  static validate(input, throwOnError) {
    return AnnotationSchema.validate(input, throwOnError);
  }

  /**
   * Convert a Block to an Annotation.
   * Note - you will need to explicitly set the range.
   * @static
   * @memberOf Annotation
   * @param {Block} block
   * @param {Object} overrides
   * @returns {Annotation}
   */
  static fromBlock(block, overrides) {
    const hasAnnotationFields = typeof block.metadata.annotation === 'object';

    const remapped = Object.assign({
      //keep the block information so we can use it for remapping later
      block,
      id: hasAnnotationFields ? block.metadata.annotation.id : undefined,
      name: block.getName(),
      description: block.metadata.description,
      color: paletteIndexToHex(block.metadata.color),
      direction: hasAnnotationFields ? block.metadata.annotation.direction : undefined,
      role: block.rules.role,
      tags: block.metadata.tags,
      notes: block.notes,
      protein: block.sequence.protein,
    }, overrides);

    return new Annotation(remapped);
  }

  /**
   * Convert an annotation instance to a Block.
   * Note - you must explicitly give the Block sequence.
   *
   * Will ignore all fields not in the schema
   * @method toBlock
   * @memberOf Annotation
   * @param {Object} overrides
   * @returns {Block}
   */
  toBlock(overrides) {
    //use only want to use some parts of this.block (as some may have changed, e.g. sequence as annotation was updated)
    const blockDefaults = Object.assign({}, this.block, {
      sequence: {
        protein: this.protein,
      },
    });

    const remapped = Object.assign(blockDefaults, {
      metadata: {
        name: this.name,
        description: this.description,
        color: hexToPaletteIndex(this.color),
        tags: this.tags,
        annotation: {
          id: this.id,
          direction: this.direction,
        },
      },
      rules: {
        role: this.role,
      },
      notes: this.notes,
    }, overrides);

    return new Block(remapped);
  }

  /**
   * Get the length of the annotation
   * @property length
   * @memberOf Annotation
   * @returns {number}
   */
  get length() {
    return this.end - this.start;
  }

  /**
   * Get the annotation role
   * @method getRole
   * @memberOf Annotation
   * @param {boolean} [userFriendly=true] Return readable text
   * @returns {string} Annotation role
   */
  getRole(userFriendly = true) {
    const friendly = symbolMap[this.role];
    return (userFriendly === true && friendly) ?
      friendly :
      this.role;
  }

  /**
   * Set the role of the annotation
   * @param role
   * @returns {Annotation}
   */
  setRole(role) {
    invariant(role === null || symbolMap[role], 'must pass valid role');
    return this.mutate('role', role);
  }

  /**
   * Set the name of the annotation
   * @param {string} name
   * @returns {Annotation}
   */
  setName(name) {
    invariant(typeof name === 'string', 'must pass string');
    return this.mutate('name', name);
  }

  /**
   *
   * @param {string} desc
   * @returns {Annotation}
   */
  setDescription(desc) {
    invariant(typeof desc === 'string', 'must pass string');
    return this.mutate('description', desc);
  }

  /**
   * mutate color
   * @param {string} color Hex string for color
   * @returns {Annotation}
   */
  setColor(color) {
    invariant(typeof color === 'string', 'must pass hex string');
    return this.mutate('color', color);
  }

  /**
   * Set the direction of the annotation
   * @param direction
   * @returns {Annotation}
   */
  setDirection(direction) {
    invariant(Directions.DirectionsAll.includes(direction), `Invalid direction ${direction}`);
    return this.mutate('direction', direction);
  }

  /**
   * Reverses the current direction of the annotation
   * @returns {Annotation}
   */
  reverseDirection() {
    return this.mutate('direction', Directions.getOppositeDirection(this.direction));
  }

  /**
   * Set the range of the annotation. Can pass either start or end, or both
   * @param {Number|null} start
   * @param {Number|null} end
   */
  setRange(start, end) {
    const gotStart = Number.isInteger(start);
    const gotEnd = Number.isInteger(end);

    if (gotStart && gotEnd) {
      return this.mutate('start', start).mutate('end', end);
    } else if (gotStart) {
      return this.mutate('start', start);
    } else if (gotEnd) {
      return this.mutate('end', end);
    }
    throw new Error('must pass start or end');
  }

  /*********
   Proteins
   *********/

  /**
   * Set the PDB IDs of the annotation protein
   * @param pdbs
   * @returns {Annotation}
   */
  setProteinPdbs(pdbs) {
    invariant(Array.isArray(pdbs) && pdbs.every(pdb => typeof pdb === 'object' && pdb.id), 'must pass array of objects, with a field ID for accesion ID');
    return this.mutate('protein.pdbs', pdbs);
  }

  /**
   * Set the molecule viewer session for the annotation protein
   * @param mvSession
   * @returns {Annotation}
   */
  setProteinMvSession(mvSession) {
    return this.mutate('protein.mvSession', mvSession);
  }
}



