import invariant from 'invariant';

import palette1 from './palette1';
import palette2 from './palette2';
import palette3 from './palette3';
import palette4 from './palette4';
import colorHashes from './colorHashes';

const paletteLength = 16;
export const colorFiller = '#888B96';
let lastIndex = 0;

export const palettes = ['Western', 'Souvenir', 'Forest', 'Neon'];

export function getPalette(name) {
  switch (name) {
    case 'Souvenir':
      return palette2;
    case 'Forest':
      return palette3;
    case 'Neon':
      return palette4;
    case 'Western':
    case 'default':
    default:
      return palette1;
  }
}

/**
 * name of palette allowing for a default
 * @param {string|null} name
 * @returns {string}
 */
export function getPaletteName(name) {
  return name || 'Western';
}

/*** next color ***/

//generate a random hex color
export function nextColor() {
  return lastIndex++ % paletteLength;
}

export function nextColorHex(palette = 'default') {
  return getPalette(palette)[nextColor()].hex;
}

export function resetColorSeed() {
  lastIndex = 0;
}

export function paletteIndexToHex(index, palette = 'default') {
  const ind = index || nextColor();
  return getPalette(palette)[ind].hex;
}

/*** hex ***/

export function isHex(val) {
  return /(^#[0-9A-F]{6}$)|(^#[0-9A-F]{3}$)/i.test(val);
}

export function randomHex() {
  return `#${Math.floor(Math.random() * (2 ** 24)).toString(16)}`;
}

//compute distance from each color in palette, and pick the closest one
//assumes both colors are 6-character hex strings: #RRGGBB
export function hexToPaletteIndex(inputHex, paletteName = 'default') {
  if (!inputHex) {
    return nextColor();
  }

  invariant(inputHex.length === 7, 'invalid hex string');
  const palette = getPalette(paletteName);
  let min = Number.MAX_SAFE_INTEGER;
  let lowestIndex = -1;
  palette.forEach(({ hex }, index) => {
    const dist = computeHexDistance(hex, inputHex);
    if (dist < min) {
      min = dist;
      lowestIndex = index;
    }
  });
  return lowestIndex;
}

export const colorByEnum = {
  Default: 'default',
  Feature: 'feature',
  Hierarchy: 'hierarchy',
};

export function getColorHashes() {
  return colorHashes;
}

//helper
function computeHexDistance(target, input) {
  if (target === input) {
    return 0;
  }
  const [tr, tg, tb] = target.slice(1).match(/.{2}/g).map(hexadec => parseInt(hexadec, 16));
  const [ir, ig, ib] = input.slice(1).match(/.{2}/g).map(hexadec => parseInt(hexadec, 16));
  return (((tr - ir) ** 2) + ((tg - ig) ** 2) + ((tb - ib) ** 2)) ** 0.5;
}



