
// the main cache is async and relies on localForage
// https://github.com/localForage/localForage

import localForage from 'localforage';
import { getPrefix } from './prefix';

const userPrefix = getPrefix().replace('-', '_');

localForage.config({
  name: userPrefix,
  description: 'Genetic Constructor Cache',
  storeName: userPrefix,
});

export default localForage;



