
//todo - should break this into constants and host in /constants/ - this isn't really inventory specific

/**
 * Genetic Constructor includes a set of roles, inspired by SBOL visual, but extended to include types which accommodate the hierarchical nature of constructs.
 * @name roles
 * @memberOf module:Constants
 */

export const symbolMap = {
  //include null for string matching, but the value will be the object null in blocks, not the string 'null'
  null: 'Blank',
  promoter: 'Promoter',
  cds: 'CDS',
  terminator: 'Terminator',
  operator: 'Operator',
  insulator: 'Insulator',
  originReplication: 'Origin of Replication',
  rbs: 'RBS',
  protease: 'Protease',
  ribonuclease: 'Ribonuclease',
  proteinStability: 'Protein Stability',
  rnaStability: 'RNA stability',
  restrictionSite: 'Restriction Site',
  structural: 'Structural',
  bidirectionalPromoter: 'Bidirectional Promoter',
  list: 'List Block',
  backbone: 'Backbone',
};

/**
 * dictates the sort order of sbol tiles
 * //todo - should be in roles.js so shared with role picker
 */
export const sortOrder = [
  'promoter',
  'cds',
  'rbs',
  'terminator',
  'protease',
  'ribonuclease',
  'rna stability',
  'protein stability',
  'origin of replication',
  'operator',
  'insulator',
  'restriction site',
  'structural',
  'bidirectional promoter',
  'plasmin backbone',
  'combinatorial list',
  'list block',
  'backbone',
  'no symbol',
];

export const symbols = Object.keys(symbolMap).map(key => ({
  id: key,
  name: symbolMap[key],
}));




