import Schema from './SchemaClass';
import fields from './fields/index';
import * as validators from './fields/validators';

/**
 * Protein information, e.g. PDB ids and molecule viewer session information
 * @name ProteinSchema
 * @memberOf module:Schemas
 * @gc Schema
 */
const fieldDefs = {
  pdbs: [
    fields.arrayOf(validators.shape({
      id: validators.string(),
      name: validators.string(),
      chain: validators.arrayOf(validators.string()),
      evalue: validators.number(),
    })),
    'List of PDB IDs similar to the sequence of the annotation / block, and their metadata. The first one is the currently active one.',
  ],
  mvSession: [
    fields.string(),
    'Molecule Viewer session ID',
    { avoidScaffold: true },
  ],
};

export class ProteinSchemaClass extends Schema {
  constructor(fieldDefinitions) {
    super(Object.assign({}, fieldDefs, fieldDefinitions));
  }
}

export default new ProteinSchemaClass();



