// Block direction constants

export const DirectionForward = 'forward';
export const DirectionReverse = 'reverse';
export const DirectionNone = 'none';
export const DirectionsAll = [DirectionForward, DirectionReverse, DirectionNone];
export const getOppositeDirection = direction => (
  direction === undefined || direction === DirectionNone || direction === DirectionForward
    ? DirectionReverse
    : DirectionForward
);



